@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin: 0px;
  padding: 0px;
}

/* Helper Styles */
.black-border{border:1px solid black}
.grey-background{background-color: #f7f7f7;}

.container-m50{margin: 0 50px;}
.display-flex{display: flex; justify-content: space-between;}
.flex-wrap{flex-wrap: wrap;}
.text-align-center{text-align: center;}
.text-align-right{text-align: right;}
.w-50{width: 50%;}
.w-100{width: 100%;}
.mb-15{margin-bottom:15px}
.mb-30{margin-bottom:30px}
.mt-15{margin-top:15px}
.mt-30{margin-top:30px}
.p-15{padding: 15px;}
.fs-12{font-size: 12px;}

/* colors */

.color.white{background-color: white}
.color.navy{background-color: #072348}
.color.black{background-color: black}
.color.red{background-color: #b5222e}
.color.yellow{background-color: #ffcf60}
.color.darkgrey{background-color: #4e4e4e}
.color.green{background-color: #129f7d}
.color.pink{background-color: #c69f97}
.color.skyblue{background-color: #215297}
.color.brown{background-color: #b3926e}
/* .color.dual::before{content: "";display:block; height:25px; width: 25px; background-color:orange; position: absolute;} */

#top-black-banner{text-align: center; color:white;background:#000;font-size: 12px;line-height: 38px;}
#navbar{
  border-bottom:1px solid #f1f1f1;
  min-height: 90px;
  margin-bottom: 50px;
}
div#hamburger-menu{padding: 25px 0px; width: 30%;}
div.hamburger-menu-icon{width: 35px;height: 5px;background-color: black;margin: 6px 0;}
#navbar ul, div#logo{width:30%}
#navbar ul li{list-style: none; display:inline;line-height: 90px; cursor: pointer; text-transform: uppercase; letter-spacing: 2px;}
ul#right-nav{display: flex; flex-direction: row-reverse;}
ul#left-nav li{margin-right: 40px;}
ul#right-nav li{margin-left: 40px;}
ul#right-nav li img{position: relative;top: 8px;}
div#logo{
  height: 50px;
  width: 80px;
  background:url(data:image/png;base64,) no-repeat;
  background:url('https://purepng.com/public/uploads/large/purepng.com-disney-logologobrand-logoiconslogos-251519939495wtv86.png') no-repeat;
  background-size: contain;
  margin-top:20px;
}
#navbar ul li:hover{text-decoration:underline}

/* Product Details */

#product-options{min-width: 30%;}
#product-info{margin-bottom: 30px;}
#product-name{font-size: 24px; font-weight: 400;margin-bottom:10px}
#product-type{font-size: 12px;}
#product-price{font-size: 16px; line-height: 32px;}
#product-reviews{font-size: 12px; line-height: 34px;}
#product-select{border: 1px solid #c8c8c8}

.gender-tab{background:#f1f1f1; line-height: 40px;display:inline-block; width: calc(50% - 0.5px);border-bottom: 1px solid #c8c8c8;cursor: pointer;}
.gender-tab.active{background:#fff; color: #7dc242;border-bottom: 1px solid #fff; z-index:2}
.tab-divider{width: 1px; background:#c8c8c8;border-bottom: 1px solid #c8c8c8;display: inline-block;height: 40px;}

#colors, #sizes{justify-content: end;margin: 0 -3px;}
span.color{
  display:inline-block;
  height: 25px;
  width: 25px;
  border-radius:50%;
  background-color:#000;
  border:1px solid #c8c8c8;
  margin: 5px;
  cursor: pointer;
  position: relative;
}
span.color.active::after{
  content: '';
  display: block;
  height: 31px;
  width: 31px;
  border: 3px solid #7dc242;
  position: absolute;
  top: -6px;
  left: -6px;
  border-radius: 50%;
}

/* Size box */

span.size-box{
  width: 35px;
  height: 35px;
  border: solid 1px #999;
  text-align: center;
  line-height: 35px;
  margin: 8px;
  cursor: pointer;
  position: relative;
}
span.size-box.active{
  background-color: #e9e9e9;
}
span.size-box.selected::before{
  content: '';
  display: block;
  height: 43px;
  width: 43px;
  border: 3px solid #7dc242;
  position: absolute;
  top: -7px;
  left: -7px;

}

#add-to-bag{
  z-index: 4;
  width: calc(100% + 2px);
  line-height: 60px;
  background: #7dc242;
  font-size: 16px;
  color: #fff;
  outline: 0px;
  border: 1px solid #7dc242;
  margin-top: -1px;
  cursor: pointer; 
}


/* Recommended Section */

#recommended-section{background:#fbfbfb;padding: 50px 0;margin-top:25px;}
#recommended-section h1{font-size: 24px;font-weight: 400;line-height: 32px;}
.slide-div{display:flex !important; justify-content: space-between;}
.slide-div-content{width:24%;}
.slide-div-content img{width: 100%;margin-bottom: 10px;}
.recommended-name{font-size:20px}

/* Features Strip */

#features-strip{
  display: flex;
  background-color: #fafafa;
  justify-content: center;
  margin-top:50px;
  color: #737373;
}
#features-strip span{
  display: block;
  line-height: 60px;
  text-align: center;
  width:30%;
  position: relative;
}
#features-strip span:nth-child(2)::before, #features-strip span:nth-child(2)::after{
  content: '';
  display: block;
  height: 40px;
  width: 1px;
  background: #cecece;
  position: absolute;
  top: 10px;
}
#features-strip span:nth-child(2)::after{right: 0px;}
.svg-inline--fa{margin-right: 10px;position: relative;top:1px}


/* Footer */

#footer{
  background:#000;
  padding: 50px 20px;
}

ul.footer-list-block{color:#fff; display: block;min-width:15%}
ul.footer-list-block li{display: block;font-size: 12px;line-height: 24px; cursor: pointer;}
ul.footer-list-block li:first-child{text-transform: uppercase;line-height: 32px;}
ul.footer-list-block li input[type="text"]{background: #000;border: 0px;border-bottom: 2px solid #fff;line-height: 30px;}

#copyright-text{color:#3a3a3a;font-size: 12px; line-height: 48px;}
#copyright-text a{color:#3a3a3a;}
#join-us-btn{background: #000;border: 2px solid #fff;color: #fff;line-height: 30px;padding: 0px 10px;margin-left: 10px;font-size: 12px;}

@media (min-width : 1025px){
  .hide-on-desktop{display:none !important}
  #product-options{width: 30%;}
  .image-gallery-thumbnails-wrapper{width:70% !important}
  .image-gallery-left-nav{left:8% !important}
  .image-gallery-right-nav{right:8% !important}
}

@media (max-width : 1024px){
  .hide-on-mobile{display:none !important}
  .invisible-on-mobile{visibility:hidden !important}

  #navbar > div{position: relative;}
  ul#left-nav{
    position: absolute;
    top: 96px;
    background: #fff;
    border: 1px solid #ccc;
  }
  ul#left-nav li{
    display: block;
    line-height: 60px;
    padding-left: 0 30px;
  }
  #product-details{display: block;}
  #product-options{width: 100%;}
  #features-strip span:nth-child(2)::before{width: 0px;}
  .slide-div{flex-wrap:wrap}
  .slide-div-content{width:49%;margin-bottom: 25px;}
  #footer-inner{flex-direction: column-reverse;}
  ul.footer-list-block{margin-bottom: 40px;}
}
