	.play-button:after {
		top: 11px;
		left: 27px;
		border-width: 8.5px 0 8.5px 12px
	}
	.close-video::before {
		font-size: 16px;
		padding: 15px
	}
}

@media (max-width:1024px) {
	.app .image-gallery,
	.app-sandbox {
		width: 100%
	}
}

.app-interval-input-group {
	display: table
}

.app-interval-label {
	display: table-cell;
	vertical-align: middle;
	padding: 6px 12px;
	font-size: 14px;
	font-weight: 400;
	line-height: 1;
	color: #555;
	text-align: center;
	background-color: #eee;
	border: 3px solid #ccc;
	border-right: none;
	border-radius: 4px;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0
}

.app-interval-input {
	-webkit-appearance: none;
	display: table-cell;
	margin: 0;
	padding: 9px;
	border-radius: 5px;
	font-size: 14px;
	border: 3px solid #ccc;
	background: #fff;
	width: 100%;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0
}

input.app-interval-input {
	width: 65px
}

.app-checkboxes {
	margin-top: 10px
}

.app-checkboxes li {
	display: block
}

